l */\n\n.back-wheel {\n  left: 20px;\n}\n\n/* Front wheel*/\n\n.front-wheel {\n  left: 440px;\n}\n\n/*ROTATION ANIMATION for CASSETTE & SPOKES ~ CW*/\n\n@-webkit-keyframes rotation {\n  from {\n    transform: rotate(-359deg);\n  }\n  to {\n    transform: rotate(0deg);\n  }\n}\n\n@keyframes rotation {\n  from {\n    transform: rotate(-359deg);\n  }\n  to {\n    transform: rotate(0deg);\n  }\n}\n\n/* Cassette for both front and back wheel */\n\n.cassette {\n  position: absolute;\n  background: white;\n  border-radius: 50%;\n  border: 4px solid black;\n  width: 20px;\n  height: 20px;\n  top: 88px;\n  left: 86px;\n  z-index: 2;\n  -webkit-animation: rotation 1s infinite linear;\n  animation: rotation 1s infinite linear;\n}\n\n/* Spokes */\n\n._5-11, ._4-10, ._3-9, ._2-8, ._1-7, ._12-6 {\n  position: absolute;\n  background: grey;\n  width: 2px;\n  height: 190px;\n  top: -87px;\n  left: 9px;\n  z-index: 1;\n}\n\n/* 360deg/12 hrs = 30deg between each hour number change */\n\n._1-7 {\n  transform: rotate(30deg);\n}\n\n._2-8 {\n  transform: rotate(60deg);\n}\n\n._3-9 {\n  transform: rotate(90deg);\n}\n\n._4-10 {\n  transform: rotate(120deg);\n}\n\n._5-11 {\n  transform: rotate(150deg);\n}\n\n/*Bike Body Frame ~6 parts */\n\n.handlebar-frame, .seat-frame, .fr-upper, .fr-lower, .bk-upper, .bk-lower {\n  position: absolute;\n  background: #ed9a18;\n  height: 14px;\n  z-index: 3;\n  border-radius: 20px;\n}\n\n.bk-lower {\n  top: 465px;\n  left: 125px;\n  transform: rotate(3deg);\n  width: 185px;\n}\n\n.bk-upper {\n  transform: rotate(-50deg);\n  width: 200px;\n  top: 385px;\n  left: 90px;\n}\n\n.fr-lower {\n  width: 260px;\n  transform: rotate(-40deg);\n  top: 390px;\n  left: 272px;\n}\n\n.fr-upper {\n  width: 250px;\n  top: 295px;\n  left: 244px;\n  transform: rotate(-10deg);\n}\n\n.seat-frame {\n  width: 235px;\n  top: 365px;\n  left: 148px;\n  transform: rotate(-111deg);\n}\n\n.handlebar-frame {\n  width: 270px;\n  top: 340px;\n  left: 375px;\n  transform: rotate(-110deg);\n